'use strict';

// MODULES //

import f16 from '@stdlib/number-float64-base-to-float16';
import toFloat32 from '@stdlib/number-float64-base-to-float32';


// MAIN //

/**
* Converts a half-precision floating-point number to the nearest single-precision floating-point number.
*
* @param {number} x - half-precision floating-point number
* @returns {number} nearest single-precision floating-point number
*
* @example
* import float64ToFloat16 from '@stdlib/number-float64-base-to-float16';
*
* var y = float16ToFloat32( float64ToFloat16( 1.337 ) );
* // returns 1.3369140625
*/
function float16ToFloat32( x ) {
	return toFloat32( f16( x ) );
}


// EXPORTS //

export default float16ToFloat32;
